"use client";

import { useParams } from "next/navigation";
import { TbXboxX } from "react-icons/tb";
import { useRouter } from "next/navigation";
import Input from "@/components/componentTag/input";
import Button from "@/components/componentTag/button";
import { useState } from "react";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import DraggableTask from "@/components/DraggableTask";
import DropColumn from "@/components/DropColumn";
import taskService from "@/services/taskService";

export default function CardDetailPage() {

    const [isForcused, setIsForcused] = useState(false)
    const [newTaskTitle, setNewTaskTitle] = useState("");
    const [tasks, setTasks] = useState([]);
    const statuses = ["Icebox", "Backlog", "Ongoing", "Review", "Done"];
  
    const router = useRouter();
    const params = useParams();
    const cardId = Number(params.id);



    useEffect(() => {
        if (!cardId) return;
        taskService
        .getByCard(cardId)
        .then((data) => setTasks(data))
        .catch((err) => console.error("Error loading tasks:", err));
    }, [cardId]);




    const handleAddTask = () => {
        if (!newTaskTitle.trim()) return;

        const newTask = {
            id: Date.now().toString(),
            title: newTaskTitle,
            status: "Icebox",
            cardId: cardId
        };

        setTasks([...tasks, newTask]);
        setNewTaskTitle("");
    };

    const handleDrop = async (taskId, newStatus) => {
        try{

            setTasks((prev) =>
                prev.map((task) =>
                    task.id === taskId ? { ...task, status: newStatus } : task
            ));
            await taskService.update(cardId, taskId, { status: newStatus });
        }
        catch(e){
             console.error(err);
        }
    }

  return (
    <DndProvider backend={HTML5Backend}>
      <div className="p-6 h-full">
        <div className="flex flex-rows justify-between">
          <h1 className="text-xl font-bold mb-4">Detail Card #{cardId}</h1>
          <TbXboxX
            onClick={() => router.push("/dashboard/card")}
            className="w-[30px] h-[30px]"
          />
        </div>

        <div className="flex flex-rows w-full h-full gap-3">
          <div className="bg-blue-300 w-1/4 h-full rounded-lg">
            <Input
              placeholder={"Add a new task"}
              onFocus={() => setIsForcused(true)}
              onBlur={() => setIsForcused(false)}
              className={"m-1"}
              value={newTaskTitle}
              onChange={(e) => setNewTaskTitle(e.target.value)}
              required={true}
            />
            {isForcused && (
              <div className="bg-gray-200 mt-1 rounded-lg h-[40px] flex items-center m-1">
                <Button
                  label="Add Task"
                  onClick={handleAddTask}
                  className={
                    "ml-2 mr-2 w-[78px] h-[30px] text-[10px] font-bold"
                  }
                />
                <Button
                  label="Cancel"
                  onClick={() => setIsForcused(false)}
                  className={
                    "w-[78px] h-[30px] text-[10px] font-bold bg-gray-200 !text-black hover:bg-gray-300"
                  }
                />
              </div>
            )}
            <div className="mt-1">
              {tasks
                .filter((task) => task.cardId === cardId)
                .map((task) => (
                  <DraggableTask key={task.id} task={task} />
                ))}
            </div>
          </div>
          <div className="bg-gray-300 w-3/4 h-full">
            <div className="grid grid-cols-5 gap-4 w-full h-full p-4 bg-white rounded-lg">
              {statuses.map((status) => (
                <DropColumn
                  key={status}
                  status={status}
                  tasks={tasks.filter(
                    (task) => task.cardId === cardId && task.status === status
                  )}
                  onDrop={handleDrop}
                />
              ))}
            </div>
          </div>
        </div>
      </div>
    </DndProvider>
  );
}